<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="bg animated fadeIn">
        <div class="kuang animated rollIn">
          <div class="title animated fadeInUp">MESSAGE</div>
          <div class="line animated fadeInUp">一</div>
          <div class="tip animated fadeInUp">诚邀您出席我们的婚礼！</div>
          <input class="inputs inputs1 animated fadeInUp" type="text" placeholder="姓名*">
          <input class="inputs inputs2 animated fadeInUp" type="text" placeholder="手机*">
          <textarea class="textareas animated fadeInUp" style="resize: none;" placeholder="祝福" name="" id="" cols="30" rows="10"></textarea>
          <input class="btn animated fadeInUp" type="button" value="提交">
          <div class="infoimg">
            <img class="fadeInUp animated" src="@/assets/logo4.png" alt srcset />
          </div>
        </div>
      </div>
      <div class="circle1 animated rotateInDownLeft">
        <circles />
      </div>
      <div class="circle2 animated rotateInDownRight">
        <circles />
      </div>
      <div class="circle3 animated rotateInDownLeft">
        <circles />
      </div>
    </div>
  </div>
</template>

<script>
import circles from "../template/circle";
export default {
  name: "page9",
  components: { circles },
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .bg {
      @include fj(center);
      align-items: center;
      $urlBg: "../../assets/photo12.png";
      position: absolute;
      @include wh(100%, 100%);
      opacity: 0;
      @include bis($urlBg);
      animation: 1s ease 1s 1 normal both running fadeIn;
      .kuang {
        $urlBg: "../../assets/kuang1.png";
        @include wh(16.084rem, 60.526vh);
        @include bis($urlBg);
        animation: 1s ease 1.2s 1 normal both running rollIn;
        .title {
          width: 100%;
          text-align: center;
          margin-top: 9vh;
          font-size: 1.1rem;
          line-height: 1.1;
          color: #000;
          animation: 1s ease 1.3s 1 normal both running fadeInUp;
        }
        .line{
            color: #000;
            font-size: 1.75rem;
            text-align: center;
            line-height: 0.6;
            animation: 1s ease 1.4s 1 normal both running fadeInUp;
        }
        .tip{
            text-align: center;
            font-size: 0.75rem;
            font-family: "宋体";
            letter-spacing: 0.05rem;
            font-weight: 600;
            color: #111;
            line-height: 1.2;
            animation: 1s ease 1.5s 1 normal both running fadeInUp;
        }
        .inputs{
            display: block;
            @include wh(9.844rem,5.4467vh);
            margin: 1.5vh auto;
            border: 1px solid #000;
            padding-left: 0.3rem;
            box-sizing: border-box;
        }
        .inputs1{
            animation: 1s ease 1.6s 1 normal both running fadeInUp;
        }
        .inputs2{
            animation: 1s ease 1.7s 1 normal both running fadeInUp;
        }
        .textareas{
              display: block;
            @include wh(9.844rem,7.2038vh);
            margin: 1.5vh auto;
            border: 1px solid #000;
            padding: 0.3rem;
            box-sizing: border-box;
            resize:none;
            animation: 1s ease 1.8s 1 normal both running fadeInUp;
        }
        .btn{
             display: block;
            @include wh(9.844rem,5.4467vh);
            margin: 1vh auto;
            background: rgb(217, 72, 87);
            color: #fff;
            font-size: 1rem;
            line-height: 5.4467vh;
            animation: 1s ease 1.9s 1 normal both running fadeInUp;
        }
        .infoimg {
          text-align: center;
          img {
            @include wh(13.301rem, 7.115vh);
            object-fit: cover;
            animation: 1s ease 2s 1 normal both running fadeInUp;
          }
        }
      }
    }

    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3 {
      @include cirlceani();
      left: -10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }
  }
}
</style>


