<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="logo animated rollIn">
        <img src="../../assets/c3.png" alt="">
      </div>
      <div class="shadow animated fadeInDown">
        <img src="../../assets/shadow.png" alt="">
      </div>
     <div class="txt animated fadeInDown">一路有你&nbsp;·&nbsp;感恩相随</div>
       <div class="photo animated rotateIn">
        <img src="../../assets/photo1.png" alt="">
      </div>
      <div class="user">
        <span class="user1 animated fadeInRight">余春娇</span>
        <img class="animated fadeIn" src="../../assets/zuan.gif" alt="">
        <span class="user2 animated fadeInLeft">张志明</span>
       
      </div>
       <div class="icon animated fadeInUp">— LOVE&nbsp;FOREVER —</div> 
        <div class="infos animated fadeInUp">
          世界上最美的一刻我们相识相知<br>沉浸在幸福中的我们<br>谨定于<br>2019年5月20日 10时举办婚礼
        </div>
        <div class="foots animated fadeInUp">诚|挚|的|邀|请|您|参|加</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "page2",
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
   overflow: hidden;
  .page{
    @include center();
    .logo{
      display: block;
      @include wh(14.7365rem,10.1904vh);
      margin: 0 auto;
      animation: 1s ease 1s 1 normal both running rollIn;
      img{
        @include wh(100%,100%);
        object-fit: cover;
      }
    }
    .shadow{
      display: block;
      @include wh(12.51rem,4.3043vh);
      margin: 0 auto;
      animation: 1s ease 1.2s 1 normal both running fadeInDown;
      img{
        @include wh(100%,100%);
        object-fit: cover;
      }
    }
    .txt{
      text-align: center;
      font-family: "宋体";
      font-size: 0.8554rem;
      font-weight: bold;
      color: #000;
      animation: 1s ease 1.3s 1 normal both running fadeInDown;
    }
    .photo{
      @include wh(17.1385rem,33.4692vh);
      margin: 1.5vh 0;
      animation: 1s ease 1.5s 1 normal both running rotateIn;
      img{
        @include wh(100%,100%);
        object-fit: cover;
      }
    }
    .user{
      text-align: center;
      font-family: "宋体";
      line-height: 1.2;
      font-size: 1rem;
      .user1{
        animation: 1s ease 1.6s 1 normal both running fadeInRight;
      }
      .user2{
        animation: 1s ease 1.8s 1 normal both running fadeInLeft;
      }
      img{
        vertical-align: text-bottom;
        @include wh(1.9045rem,5.182vh);
            animation: 1s ease 1.9s 1 normal both running fadeIn;
      }
    }
    .icon{
      text-align: center;
      font-family: "宋体";
      line-height: 1.2;
      font-size: .6rem;
      letter-spacing: 0.2rem;
      margin-top: 1vh;
      animation: 1s ease 2s 1 normal both running fadeInUp;
    }
    .infos{
      text-align: center;
       margin-top: 1vh;
       color: rgb(103, 103, 103);
       line-height: 1.8;
       font-size: 0.7rem;
       animation: 1s ease 2.1s 1 normal both running fadeInUp;
    }
    .foots{
       font-family: "宋体";
      text-align: center;
       margin-top: 2vh;
        font-size: 0.7rem;
        letter-spacing: 0.2rem;
        color: #222;
        animation: 1s ease 2.2s 1 normal both running fadeInUp;
    }
  }
}
</style>


