<template>
  <div class="swiper-slide1">
    <div class="page">
      <div class="star1 animated flash"></div>
      <div class="star2 animated swing"></div>
      <div class="info">
        <div class="logo animated fadeInDown">
          <img src="../../assets/logo.png" alt="" srcset="">
        </div>
        <div class="user"><span class="user1 animated fadeInRight">Mr.张志明</span> <img class="xing" src="../../assets/stars.png" alt="" srcset=""> <span class="user2 animated fadeInLeft">余春娇.Miss</span></div>
        <div class="time animated fadeInUp">&#60;202X/05/20 10:00&#62;</div>
        <div class="tips animated fadeInUp">·诚邀您参加我们的婚礼·</div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "page1",

  data() {
    return {};
  }
};
</script>

<style scoped lang="scss">
@import "src/style/mixin";
@import "src/style/ani";
.swiper-slide1 {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  .page {
    $urlBg:"../../assets/bg.jpg";
    position: absolute;
    @include wh(100%, 100%);
    @include bis($urlBg);
    .star1{
      $urlBg1:"../../assets/star1.png";
      @include wh(15.6445rem,23.4557vh);
      margin: 1.84rem auto 0;
      @include bis($urlBg1);
      animation: 1s ease 1s infinite normal both running flash;
    }
    .star2{
      @include pos(-1.15rem,1.04rem);
      $urlBg1:"../../assets/star2.png";
      @include wh(5.919rem,20.2233vh);
      @include bis($urlBg1);
      animation: 5s ease 1s infinite normal both running swing;
    }
    .info{
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 9vh;
      .logo{
        display: block;
        @include wh(15.0295rem,15.0224vh);
        margin: 0 auto;
        animation: 1s ease 1s 1 normal both running fadeInDown;
        img{
           @include wh(15.0295rem,15.0224vh);
           object-fit: cover;
        }
      }
      .user{
        width: 100%;
        text-align: center;
        font-size: 0.875rem;
        line-height: 1.6;
        margin-top: 2vh;
        letter-spacing: 0.15rem;
        .user1{
          animation: 1s ease 1.2s 1 normal both running fadeInRight;
        }
        .user2{
          animation: 1s ease 1.4s 1 normal both running fadeInLeft;
        }
        .xing{
          @include wh(1.465rem,1.201rem);
          vertical-align:middle;
          opacity: 0.4;
          margin: 0 0.304rem;
          animation: 1s ease 1.5s 1 normal both running fadeIn, 1s ease 1s infinite normal forwards running pulse;
        }
      }
      .time{
         width: 100%;
        text-align: center;
        font-size: 0.875rem;
        line-height: 1.6;
        margin-top: 2vh;
        color: #333;
        letter-spacing: 0.1rem;
        animation: 1s ease 1.7s 1 normal both running fadeInUp;
      }
      .tips{
         width: 100%;
        text-align: center;
        font-size: 0.875rem;
        line-height: 1.6;
        margin-top: 2vh;
        letter-spacing: 0.15rem;
        animation: 1s ease 2s 1 normal both running fadeInUp;
      }
    }
  }
}
</style>


