<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="content">
        <div class="map animated fadeInDown">
          <img src="@/assets/v2.png" alt="">
          <a class="send" href="http://api.map.baidu.com/geocoder?address=武汉大学&output=html&src=webapp.baidu.openAPIdemo">导航</a>
        </div>
        <div class="logo animated rotateIn">
          <img src="@/assets/logo5.png" alt="">
        </div>
        <div class="infos infos1 animated fadeInUp">
          设席:合肥市绿地赢海大酒店三楼
        </div>
        <div class="infos infos2 animated fadeInUp">
          时间:2019年5月20日 上午10:00
        </div>
        <div class="btns">
          <button @click="call" class="btn1 fadeInRight animated"><img src="@/assets/phone.png" alt="">联系新娘</button>
          <button @click="call" class="btn2 fadeInLeft animated"><img src="@/assets/phone.png" alt="">联系新郎</button>
        </div>
        <div class="infoimg fadeInUp animated">
            <img src="@/assets/logo4.png" alt srcset />
        </div>
      </div>
      <circles class="circle1 animated rotateInDownLeft" />
      <circles class="circle2 animated rotateInDownRight" />
      <circles class="circle3 animated rotateInDownLeft" />
    </div>
  </div>
</template>

<script>
import circles from "../template/circle";
export default {
  name: "page6",
  components: { circles },
  data() {
    return {};
  },
  methods:{
    call(){
       window.location.href = 'tel://18505365925'
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .content {
      width: 100%;
      @include center();
      .map{
        position: relative;
        display: block;
        @include wh(15.525rem,28.6356vh);
        margin: 0 auto;
        animation: 1s ease 1.1s 1 normal both running fadeInDown;
        img{
          @include wh(100%,100%);
          object-fit: cover;
        }
        .send{
          position: absolute;
          right: 0.2rem;
          bottom: 0.2rem;
          display: block;
          @include wh(2rem,0.667rem);
          text-align: center;
          line-height: 0.667rem;
          background: #1bceb3;
          color: #fff;
          font-size: 0.6rem;
          border-radius: 0.1rem;
          overflow: hidden;
        }
      }
      .logo{
        display: block;
        margin: 0 auto;
        @include wh(17.3145rem,18.9745vh);
        animation: 1s ease 1.3s 1 normal both running rotateIn;
        img{
          @include wh(100%,100%);
          object-fit: cover;
        }
      }
      .infos{
        text-align: center;
        font-family: "宋体";
        font-size: 1rem;
        font-weight: bold;
        line-height: 2;
      }
      .infos1{
        animation: 1s ease 1.4s 1 normal both running fadeInUp;
      }
      .infos2{
        animation: 1s ease 1.5s 1 normal both running fadeInUp;
      }
      .btns{
        @include fj(space-around);
        align-items: center;
        margin-top: 3vh;
        .btn1{
          animation: 1s ease 1.6s 1 normal both running fadeInRight;
        }
        .btn2{
          animation: 1s ease 1.7s 1 normal both running fadeInLeft;
        }
        button{
          @include wh(7.025rem,6.296vh);
          background: #000;
          border-radius: 0.4rem;
          overflow: hidden;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #fff;
          font-size: 0.9rem;
          
          img{
            @include wh(1.079rem,1.079rem);
            margin-right: 0.1rem;
          }
        }
      }
      .infoimg{
        display: block;
        margin: 3vh auto;
        @include wh(12.8905rem,6.851vh);
        animation: 1s ease 1.8s 1 normal both running fadeInUp;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3 {
      @include cirlceani();
      left: -10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }
  }
}
</style>


