<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="bg animated fadeIn"></div>
      <div class="circle1 animated rotateInDownLeft"><circles/></div>
      <div class="circle2 animated rotateInDownRight"><circles/> </div>
      <div class="circle3 animated rotateInDownLeft"><circles/></div>
      <div class="kuang animated fadeIn">
        <img class="photo1 animated fadeInUp" src="../../assets/photo4.png" alt />
        <img class="photo2 animated fadeInUp" src="../../assets/photo5.png" alt />
        <img class="photo3 animated fadeInUp" src="../../assets/photo6.png" alt />
      </div>
      <div class="info">
        <div class="logo-group animated rotateIn">
          <img class="logo1" src="@/assets/logo1.png" alt="" srcset="">
          <img class="logo2" src="@/assets/xing1.png" alt="" srcset="">
          <img class="logo3" src="@/assets/xing1.png" alt="" srcset="">
        </div>
        <div class="user animated fadeInUp"> 新娘:余春娇</div>
        <div class="bless animated fadeInUp">有你陪伴的幸福每一秒都不后悔过去未来我陪你体会</div>
      </div>
    </div>
  </div>
</template>

<script>
import circles from "../template/circle"
export default {
  name: "page3",
  components:{circles},
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .bg {
      $urlBg: "../../assets/photo3.jpg";
      position: absolute;
      @include wh(100%, 100%);
      opacity: 0;
      @include bis($urlBg);
      animation: 1s ease 1s 1 normal both running fadeIn;
    }
    .kuang {
      @include wh(5.9765rem, 47.1739vh);
      position: absolute;
      right: 1rem;
      bottom: 6.4vh;
      @include bis("../../assets/kuang.png");
      animation: 1s ease 1.2s 1 normal both running fadeIn;
      @mixin commonani($timer, $h) {
        @include wh(4.248rem, 9.9265vh);
        object-fit: cover;
        animation: 1s ease $timer 1 normal both running fadeInUp;
        @include pos(1rem, $h);
      }
      .photo1 {
        @include commonani(1.4s, 5.5vh);
      }
      .photo2 {
        @include commonani(1.6s, 20.2vh);
      }
      .photo3 {
        @include commonani(1.8s, 34.2vh);
      }
    }
    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3{
      @include cirlceani();
      left:-10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .info{
      position: absolute;
      left: 0;
      bottom: 9vh;
      @include wh(12.823rem,auto);
      .logo-group{
        position: relative;
        animation: 1s ease 2s 1 normal both running rotateIn;
        .logo1{
          @include wh(8.7305rem,12.65vh);
          margin: 0 auto;
          display: block;
        }
        .logo2{
          @include wh(0.5525em,0.586rem);
          @include pos(7.5rem,0.65rem);
        }
        .logo3{
          @include wh(0.879em,0.996rem);
          @include pos(8.1rem,0.95rem);
        }
      }
      .user{
        letter-spacing: 0.2rem;
        font-family: "宋体";
        font-size: 1rem;
        line-height: 1.2;
        border: 0.2rem solid dashed;
        text-align: center;
        color:#fff;
        animation: 1s ease 2.4s 1 normal both running fadeInUp;
      }
      .bless{
         width: 90%;
        letter-spacing: 0.1rem;
        font-family: "宋体";
        font-size: 0.7rem;
        line-height: 1.6;
        text-align: center;
        color:#fff;
        margin: 2vh auto 0;
        font-weight: bold;
        animation: 1s ease 2.6s 1 normal both running fadeInUp;
      }
    }
  }
}
</style>


