<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="content">
        <div class="logo animated rotateIn">
          <img src="@/assets/logo2.png" alt />
        </div>
        <div class="photo">
          <img class="animated fadeIn bgphoto" src="@/assets/photo7.png" alt />
          <div class="photo_wrap">
            <div class="top_logo animated fadeInDown">
              <img src="@/assets/photo8.png" alt />
            </div>
            <div class="photo_lists">
              <div class="photo_1 animated fadeInLeft">
                <img src="@/assets/photo9.png" alt />
              </div>
              <div class="photo_2 animated fadeInUp">
                <img src="@/assets/photo10.png" alt />
              </div>
              <div class="photo_3 animated fadeInRight">
                <img src="@/assets/photo11.png" alt />
              </div>
            </div>
          </div>
        </div>

        <div class="title animated rotateInDownLeft">“ 遇|见|爱|情|遇|见|幸|福 ”</div>
        <div class="bless animated rotateInUpRight">MEET LOVE, MEET HAPPINESS</div>
        <div class="line_logo">
          <img class="line_logo_img animated fadeInUp" src="@/assets/line.gif" alt />
          <img class="xing animated fadeInUp pulse" src="@/assets/stars.png" alt />
        </div>
      </div>
      <circles class="circle1 animated rotateInDownLeft" />
      <circles class="circle2 animated rotateInDownRight" />
      <circles class="circle3 animated rotateInDownLeft" />
    </div>
  </div>
</template>

<script>
import circles from "../template/circle";
export default {
  name: "page7",
  components: { circles },
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .content {
      width: 100%;
      @include center();
      .logo {
        display: block;
        margin: 2vh auto;
        @include wh(10.4885rem, 11.947vh);
        animation: 1s ease 1.2s 1 normal both running rotateIn;
        img {
          @include wh(100%, 100%);
          object-fit: cover;
        }
      }
      .photo {
        position: relative;
        display: block;
        margin: 0 auto;
        @include wh(14.707rem, 45.68vh);

        .bgphoto {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 2;
          @include wh(100%, 100%);
          object-fit: cover;
          border: 0.07rem solid #222;
          animation: 3s ease 3s 1 normal both running fadeIn;
        }
        .photo_wrap {
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          margin: 0 auto;
          @include wh(14.707rem, 45.68vh);
          font-size: 0;
          .top_logo {
            @include wh(100%, 29.517vh);
                animation: 1s ease 1.4s 1 normal both running fadeInDown;
            img {
              @include wh(100%, 100%);
              object-fit: cover;
            }
          }
          @mixin photo_list {
            display: inline-block;
            @include wh(4.629rem, 13.88vh);
            img {
              @include wh(100%, 100%);
              object-fit: cover;
            }
          }
          .photo_lists {
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .photo_1 {
              @include photo_list();
              animation: 1s ease 1.6s 1 normal both running fadeInLeft;
            }
            .photo_2 {
              @include photo_list();
              animation: 1s ease 1.8s 1 normal both running fadeInUp;
            }
            .photo_3 {
              @include photo_list();
              animation: 1s ease 2s 1 normal both running fadeInRight;
            }
          }
        }
      }

      .title {
        width: 100%;
        text-align: center;
        font-size: 0.95rem;
        letter-spacing: 0.24rem;
        line-height: 3.2;
        animation: 1s ease 2.4s 1 normal both running rotateInDownLeft;
      }
      .bless {
        width: 100%;
        text-align: center;
        font-size: 0.65rem;
        letter-spacing: 0.1rem;
        animation: 1s ease 2.5s 1 normal both running rotateInUpRight;
      }
      .line_logo {
        @include wh(100%, 10.805vh);
        position: relative;
        text-align: center;
        // display: flex;
        // justify-content: center;
        // align-items: center;
        .line_logo_img {
          position: relative;
          @include wh(10.8985rem, 10.805vh);
          animation: 1s ease 2.3s 1 normal both running fadeInUp;
        }
        .xing {
          position: absolute;
          left: 50%;
          top: 50%;
          margin-left: -1.33775rem;
          margin-top: -1.205rem;
          @include wh(2.6755rem, 2.41rem);
          opacity: 0.6;
          animation: 1s ease 2.4s 1 normal both running fadeInUp,
            1s ease 1s infinite normal forwards running pulse;
        }
      }
    }

    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3 {
      @include cirlceani();
      left: -10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }
  }
}
</style>


