<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="bg animated fadeIn">
          <div class="kuang animated rollIn">
              <div class="title animated fadeInDown">【婚礼流程】</div>
              <div class="info animated info1 fadeInDown">AM：08:00-09:00   迎接新娘</div>
              <div class="info animated info2 fadeInDown">AM：09:00-10:00   宾客入席</div>
              <div class="info animated info3 fadeInDown">AM：09:00-10:00   婚礼开始</div>
              <div class="info animated info4 fadeInDown">AM：09:00-10:00   宴会开始</div>
              <div class="info animated info5 fadeInDown">AM：09:00-10:00   宴会结束</div>
              <div class="infoimg">
                  <img class="fadeInUp animated" src="@/assets/logo4.png" alt="" srcset="">
              </div>
          </div>
      </div>
      <div class="circle1 animated rotateInDownLeft"><circles/></div>
      <div class="circle2 animated rotateInDownRight"><circles/> </div>
      <div class="circle3 animated rotateInDownLeft"><circles/></div>

    </div>
  </div>
</template>

<script>
import circles from "../template/circle"
export default {
  name: "page8",
  components:{circles},
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .bg {
      @include fj(center);
      align-items: center;
      $urlBg: "../../assets/bg.jpg";
      position: absolute;
      @include wh(100%, 100%);
      opacity: 0;
      @include bis($urlBg);
      animation: 1s ease 1s 1 normal both running fadeIn;
      .kuang{
          $urlBg: "../../assets/kuang1.png";
          @include wh(16.084rem,60.526vh);
          @include bis($urlBg);
          animation: 1s ease 1.2s 1 normal both running rollIn;
          .title{
              width: 100%;
              height: 7.813vh;
              line-height: 7.813vh;
              text-align: center;
              margin-top: 8vh;
              font-size: 1.1rem;
              animation: 1s ease 1.4s 1 normal both running fadeInDown;
          }
          @mixin infoani($timer){
              animation: 1s ease $timer 1 normal both running fadeInDown;
          }
          .info{
              width: 100%;
              text-align: center;
              font-size: 0.8rem;
              color: #111;
              line-height: 6.5vh;
          }
          .info1{
              @include infoani(1.5s);
          }
          .info2{
              @include infoani(1.6s);
          }
          .info3{
              @include infoani(1.7s);
          }
          .info4{
              @include infoani(1.8s);
          }
          .info5{
              @include infoani(1.9s);
          }
          .infoimg{
              text-align: center;
              img{
                  @include wh(13.301rem,7.115vh);
                  object-fit: cover;
                  animation: 1s ease 1.4s 1 normal both running fadeInUp;
              }
          }
      }
    }

    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3{
      @include cirlceani();
      left:-10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }
  }
}
</style>


