<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="content">
        <div class="logo_love animated fadeInDown">
          <img src="@/assets/love.png" alt="">
        </div>
        <div class="plane_logo animated rotateIn">
          <img src="@/assets/plane.gif" alt="">
        </div>
        <div class="top">
          <div class="desc animated fadeInRight">
            <div class="title animated fadeInUp">相识</div>
            <div class="shi animated fadeInUp">BOSOM FRIEND</div>
            <div class="logo_user animated fadeInUp">
              <img src="@/assets/logo3.png" alt="">
            </div>
            <div class="txt animated fadeInUp">
              雨夜一直是恋人之间浪漫的开始，<br>与你相遇的也正好是一个下着绵绵细雨的夜晚
            </div>
          </div>
          <div class="photo animated fadeInLeft">
            <img class="rightLogo" src="@/assets/photo.jpg" alt="">
            <img class="niukou animated fadeIn" src="@/assets/niukou.png" alt="">
          </div>
        </div>
        <div class="bigphoto animated rollIn">
          <img src="@/assets/photo4.jpg" alt="">
        </div>
      </div>
      <div class="circle1 animated rotateInDownLeft"><circles/></div>
      <div class="circle2 animated rotateInDownRight"><circles/> </div>
      <div class="circle3 animated rotateInDownLeft"><circles/></div>

    </div>
  </div>
</template>

<script>
import circles from "../template/circle"
export default {
  name: "page3",
  components:{circles},
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  overflow: hidden;
  .page {
    position: absolute;
    @include wh(100%, 100%);
    @mixin cirlceani() {
      position: absolute;
      @include wh(60vw, 60vw);
      opacity: 0;
    }
    .content{
      @include center();
      .logo_love{
        @include wh(16.289rem,4.655vh);
        margin: 7.5427vh auto 9.5427vh;
        animation: 1s ease 1s 1 normal both running fadeInDown;
        img{width: 100%;height: 100%;object-fit: cover;}
      }
      .plane_logo{
        @include pos(0,0);
        @include wh(11.367rem,8.174rem);
        z-index: -1;
        animation: 1s ease 1.2s 1 normal both running rotateIn;
      }
      .top{
        @include wh(90.66vw,33.88vh);
        font-size: 0;
        margin: 0 auto;
        .desc{
          display: inline-block;
          vertical-align: top;
          @include wh(48%,33.88vh);
          padding: 0 0.3rem;
          margin: 0 2% 0 0;
          box-sizing: border-box;
          border: 0.1rem solid #000;
          animation: 1s ease 1.6s 1 normal both running fadeInRight;
          .title{
            @include wh(100%,4.902vh);
            @include font(1.15rem,4.902vh,"宋体");
            vertical-align: bottom;
            text-align: center;
            font-weight: 600;
            animation: 1s ease 1.8s 1 normal both running fadeInUp;
          }
          .shi{
            color: rgb(103, 103, 103);
            @include font(0.6rem,0.6rem);
            text-align: center;
            animation: 1s ease 1.9s 1 normal both running fadeInUp;
          }
          .logo_user{
            display: block;
            margin: 1vh auto 2vh;
            @include wh(2.5rem,8.695vh);
            animation: 1s ease 2.1s 1 normal both running fadeInUp;
            img{width: 100%;height: 100%;object-fit: cover;}
          }
          .txt{
            text-align: center;
            font-size: 0.7rem;
            color: rgb(38, 38, 38);
            line-height: 1.5;
            animation: 1s ease 2.4s 1 normal both running fadeInUp;
          }
        }
        .photo{
          position: relative;
          display: inline-block;
          vertical-align: top;
          @include wh(50%,33.88vh);
          animation: 1s ease 1.4s 1 normal both running fadeInLeft;
          .rightLogo{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          .niukou{
            @include wh(102.15%,4.56vh);
            position: absolute;
            z-index: 1;
            bottom: -0.4rem;
            left: 0;
            animation: 1s ease 2.5s 1 normal both running fadeIn;
            img{
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
        }
      }
      .bigphoto{
        display: block;
        @include wh(94.93vw,26.79vh);
        margin: 0.4vh auto 0;
        animation: 1s ease 2.6s 1 normal both running rollIn;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .circle1 {
      @include cirlceani();
      left: -10vw;
      top: 10vw;
      animation: 1s ease 0.1s 1 normal none running rotateInDownLeft;
    }
    .circle2 {
      @include cirlceani();
      right: -10vw;
      top: 10vw;
      animation: 1.1s ease 0.2s 1 normal none running rotateInDownRight;
    }
    .circle3{
      @include cirlceani();
      left:-10vw;
      bottom: 10vw;
      animation: 1.2s ease 0.1s 1 normal none running rotateInDownLeft;
    }

  }
}
</style>


